var data = {
code: this.code,
state: ""
};
this.$fetch.post(url.getOpenId, data).then(res => {
console.log(res);
localStorage.setItem("openId", res.obj.openid);
localStorage.setItem("nickname", res.obj.nickname);
localStorage.setItem("headimgurl", res.obj.headimgurl);
});
