import axios from 'axios';
import router from '../router';
import Vue from 'vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)

const config = require('../config/config.dev')

let options = {
baseURL: config.baseURL,
timeout: 300000,
headers: {
'Content-Type': 'application/json',
'AuthorizationKey': window.localStorage.user || '',
}

}
let service = axios.create(options)


service
.interceptors
.request
.use(request => {
request.headers = { "AuthorizationKey": window.localStorage.getItem("user") };

return request
}, error => {
console.log(error)
Promise.reject(error)

})


service
.interceptors
.response
.use(response => {

/**
* code为非200是错误的请求
*/

var aUrl = 'https://open.weixin.qq.com/connect/oauth2/authorize?' + '?appid=' + response.data.obj + '&redirect_uri=http://www.gsb.yuntunet.cn/%23/&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
if (response.data.code == "702") {
window.location.href = aUrl
}
if (response.data.msg == "openId_none!") {
window.location.href = aUrl
}
if (response.data.msg == "openId_error") {
window.location.href = aUrl
}
if (response.data.msg == "openId_error!") {
window.location.href = aUrl
}
if (response.data.msg == "no_login") {
router.push('/login/1');
}
if (response.data.code == 300) {
alert(response.data.message)




router.push('/login/1');
}

if (response.status !== 200) {
return Promise.reject('error')
} else {



return response.data
}
}, error => {
console.log(error)








return Promise.reject(error)
})

export default service


